#FeedPost #containerRect {
  position: relative;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.9);
}

#FeedPost #postImage {
  /* parent is relative */
  position: relative;
  width: 100%;
}

#FeedPost #authorContainer {
  position: absolute;
  background: linear-gradient(251.4deg, #56a190 31.02%, #2c5241 106.95%);
}

#FeedPost #profileImg {
  position: absolute;
}

#FeedPost #authorName {
  position: absolute;
  font-weight: bold;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #ffffff;
  margin-top: auto;
  margin-bottom: auto;
}

#FeedPost #artTitle {
  /* positioning is relative to container Rect */
  position: relative;
  text-align: left;
}

#FeedPost .commentSec {
  position: relative;
  overflow: auto;
  width: 100%;
}

#FeedPost .commentDiv {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
